import './Header.scss';
import $ from 'jquery';
import { useEffect } from 'react';
import { streamer } from './utils/Config';
import { datosFirebaseUsuario } from './utils/Firebase';

const Header = () => {

    useEffect(() => {
        datosFirebaseUsuario()
        .then((data) => {
            let logo = data.logo ? data.logo : './img/logo.png';
            $('#logo').attr({
                src: logo
            });
        });

        let contador = 0;
        obtainStatus()
        let minutos = 1
        let ms = minutos * 60 * 1000
        
        let rellamadas = setInterval(() => {
            obtainStatus()
        }, ms)

        $('.cargando').show()
        function obtainStatus() {
            contador++
            if (contador >= 5) {clearInterval(rellamadas)}
            $.get({
                url: `https://twitch-check.glitch.me/${streamer}`,
                cache: false,
                success : function(response){
                    if (response) {
                        $('.cargando').hide();
                        if (response.online) {
                            $('#texto-live').html('ONLINE: ' + response.stream_viewers + '<i class="fa fa-eye ml-1"></i>')
                            $('.logo-txt').addClass('logo-animation')
                            $('#texto-live').removeClass('texto-offline').addClass('texto-online')
                            $('#punto-live').removeClass('punto-offline').addClass('punto-online')
                        } else {
                            $('#texto-live').text('OFFLINE')
                            $('.logo-txt').removeClass('logo-animation')
                            $('#texto-live').removeClass('texto-online').addClass('texto-offline')
                            $('#punto-live').removeClass('punto-online').addClass('punto-offline')
                        }
                    } else {
                        setTimeout(() => {
                            obtainStatus()
                        },3000)
                    }
                }
            })
        }
    })

    return (
        <>

            <div className="buttons">
                <div className="col p-2 mt-2">
                    <button className="enlace">
                        <h1 className="logo-txt">KEVINSILLO</h1>
                        <img className="logo-img" alt="Logo" id="logo" />
                    </button>
                    <div className="live d-flex">
                        <div id="punto-live"></div>
                        <div id="texto-live"></div>
                        <div className="cargando">Cargando status...</div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Header;